"use client";

import { useRef, useEffect } from "react";
import { AlphaTabApi, Settings, LayoutMode } from "@coderline/alphatab";
import '@/remotion/player.css';
import { Box } from '@mui/material';

interface AlphaTabViewerProps {
  xmlPath: string;
  layoutMode?: 'page' | 'horizontal';
  onScoreLoaded?: (score: any) => void;
  onPlayerPositionChanged?: (e: any) => void;
}

export const AlphaTabViewer = ({ xmlPath, onScoreLoaded, onPlayerPositionChanged, layoutMode = 'horizontal' }: AlphaTabViewerProps) => {
  const elementRef = useRef<HTMLDivElement>(null);
  const apiRef = useRef<AlphaTabApi | null>(null);
  useEffect(() => {
    if (!elementRef.current || !xmlPath) return;

    const baseUrl = window.location.origin;
    const settings: Settings = {
      core: {
        fontDirectory: `${baseUrl}/alphaTab/font/`,
        scriptFile: `${baseUrl}/alphaTab/alphaTab.js`
      },
      player: {
        enablePlayer: true,
        enableCursor: true,
        soundFont: `${baseUrl}/alphaTab/soundfont/sonivox.sf2`
      }
    };

    const api = new AlphaTabApi(elementRef.current!, settings);
    apiRef.current = api;

    if (onScoreLoaded) {
      apiRef.current.scoreLoaded.on(onScoreLoaded);
    }
    if (onPlayerPositionChanged) {
      apiRef.current.playerPositionChanged.on(onPlayerPositionChanged);
    }

    // Sử dụng prop xmlPath để load file nhạc
    apiRef.current.load(xmlPath);

    return () => {
      if (apiRef.current) {
        apiRef.current?.destroy();
      }
    };
  }, [xmlPath, onScoreLoaded, onPlayerPositionChanged]);
  return (
    <Box sx={{ width: '100%', position: 'relative', paddingTop: '56.25%' }}>
      {/* 16:9 = 9/16 = 0.5625 => 56.25% */}
      <Box
        sx={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'lightblue',
        }}
      >
        <div ref={elementRef} style={{ width: '100%', height: '100%' }} />;
      </Box>
    </Box>
  );
};